import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const TotalDeposit = props => {
  const { handleChange, totalDeposit } = props;
  return (
    <TextValidator
      required
      name="totalDeposit"
      id="totalDeposit"
      onChange={handleChange}
      value={totalDeposit}
      label="Total Deposit"
      helperText=""
      placeholder="0000.00"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={[
        'required',
        'isFloat',
        'isCorrectPaymentAmount',
      ]}
      errorMessages={[
        '',
        '',
        ' 16   2 ',
      ]}
    />
  );
};

export default TotalDeposit;
