import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const TotalTax = props => {
  const { handleChange, totalTax } = props;
  return (
    <TextValidator
      required
      name="totalTax"
      id="totalTax"
      onChange={handleChange}
      value={totalTax}
      label="Total Tax"
      helperText=""
      placeholder="0000.00"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={[
        'required',
        'isFloat',
        'isCorrectPaymentAmount',
      ]}
      errorMessages={[
        '',
        '',
        ' 16   2 ',
      ]}
    />
  );
};

export default TotalTax;
